.form-selection__title {
  font-family: $font_oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: $black;
  text-align: start;
  margin: 50px 0 25px 0;
}

.form-selection__text {
  font-family: $font_oswald;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: $black;
  text-align: start;
  text-transform: uppercase;
  margin-bottom: 40px;
}

@media (min-width: $tablet-width) {
  .form-selection__wrapper {
    padding: 0 40px;
    box-sizing: border-box;
    margin-bottom: 70px;
  }
}

@media (min-width: $desktop-width) {
  .form-selection__wrapper {
    padding: 0;
  }
}
